@font-face {
  font-family: 'StockmannSans';
  src: url('./fonts/StockmannSans-Light.woff2') format('woff2'),
  url('./fonts/StockmannSans-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'StockmannSans';
  src: url('./fonts/StockmannSans-Bold.woff2') format('woff2'),
  url('./fonts/StockmannSans-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}


html, body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  box-sizing: border-box;
  background: #254234;
}
div, canvas {
  box-sizing: border-box;
}





